/*
 * Globals
 */

/* Custom default button */
.btn-secondary,
.btn-secondary:hover,
.btn-secondary:focus {
  color: #333;
  text-shadow: none; /* Prevent inheritance from `body` */
}


/*
 * Base structure
 */
 html, body, #root, .App {
  height: 100%;
}

html, body, #root, .App {
  text-shadow: 0 .05rem .1rem rgba(0, 0, 0, .5);
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
}

.cover-container {
  max-width: 48rem;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
}

/*
 * Header
 */

 .nav-masthead .nav-link {
  padding: .25rem 0;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--off-white);
  background-color: transparent;
  border-bottom: .25rem solid transparent;
}

.nav-masthead .nav-link:hover,
.nav-masthead .nav-link:focus {
  color: #fff;
  border-bottom-color: var(--teal);
}

.nav-masthead .nav-link + .nav-link {
  margin-left: 1rem;
}

.nav-masthead .active {
  color: #fff;
  border-bottom-color: #fff;
}

/* Background images obtained from: www.pexels.com/ */
/* Color palette generated from https://colorhunt.co/ */

/* variables created for color scheme */
:root {
  --off-white: #e7e7de;
  --teal: #62959c;
  --dark-blue: #00587a;
  --midnight-blue: #0f3057;
}

#root {
  background-image: url(./assets/images/wood-grain.jpg);
  background-size: cover; /* this makes the background image stretch to fit the entire space, or else it would repeat itself */
  display: flex;
  justify-content: center;
  align-items: center;
}

.portfolio:hover {
cursor:pointer;
}

#nav {
justify-self: flex-start;
}

.visible {
  visibility: visible;
}

.no-display {
  display: none;
}

/* overriding Bootstrap stying */
.into-margin {
  margin-bottom: 30%!important; 
}

#intro h1 {
  font-size: 4.5rem;
  color: var(--off-white);
}

h3 {
  color: var(--off-white);
  font-size: 2.5rem;
}

p {
  margin-bottom: 0.25rem;
}

#intro .lead {
  font-size: 3.5rem;
  color: var(--off-white);
  font-weight: 400;
}

#intro .btn-intro {
  background-color: var(--off-white);
  color: var(--dark-blue);
}

#intro .btn-intro:hover, #intro .btn-intro:focus {
  background-color: var(--teal);
  color: var(--off-white);
}